.contact-card-container {
  min-width: 400px;
  max-width: 400px;
  min-height: 400px;
  margin: 10px;
  padding: 30px 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  /*border: 1px solid #000;*/
}

.user-round {
  width: 200px;
  height: 200px;
  margin-bottom: 1em;
  border: 1px solid #333;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2em;
}
